import "./Main.css";

import {styled} from "styled-components";
import { useEffect, useState } from "react";

const CircleBg = styled.div`
    width: 100%;
    max-width: 1400px;
    height:800px;
    overflow-x: hidden;
    background-color: #95CE9E;
    border-radius: 555px;
    position: absolute;
    bottom: -5%;
    left: 50%;
    transform: translateX(-50%);
    transition: .5s;
    &.active{
        max-width: 100%;
        height: 100%;
        bottom: 0;
        border-radius: 0;
    }
    @media(max-width:1400px){
        
        border-radius: 555px 555px 0 0;
    }
    @media(max-width:768px){
        height: 565px;
    }
`
const InfoT = styled.div`
     margin-top: 50px;
     & p{
        font-size: 15px ;
        font-weight: 400;
        line-height: 1.5;
     }
`
// const randomNumber = (max:number, min:number)=>{
//    return Number(Math.random() * (max-min+1) + min);
// }
function Main({el1} : any){
    const [isZoom, setIsZoom] = useState(true);

    const handleScroll = () => {
        const scrolled = window.scrollY;
      if (scrolled > 0) {
        setIsZoom(false);
        } else {
        setIsZoom(true);
      }
    };
    let vh = 0;

    useEffect(() => {
      vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    }, []);
    useEffect(() => {
        window.addEventListener('scroll', handleScroll);
      return () => {
        window.removeEventListener('scroll', handleScroll);
      };
      
    }, []);

    return(
        <>
        <section id="Main"  ref={el1}>
                <div className={`Title_wrap ${isZoom ? '' : 'scroll'}`}>
                    <h1 className=" noinfo">
                       
                        Je Protfolio
                        <span>
                         IT     <br />     
                        </span>
                  
                    </h1>
                    <h1 className="en infoTxt">
                       Thank you for Watching!
                        <span>
                             !
                        </span>
                        <InfoT>
                            <p>
                                   .<br />
                                   . 
                            </p>
                        </InfoT>
                    </h1>
                </div>
   
            <CircleBg id="circleBg" className={`${isZoom ? '' : 'active'}`}>
                <div className="txt_wrap">
                    <h6>
                        ~ :D  . <br />
                                . <br />
                         !
                    </h6>
                </div>
                <div className="img_wrap">
                    <img src={process.env.PUBLIC_URL + `/images/my1.gif`} alt="" className="img-fluid-block"/>
                </div>
            </CircleBg>

        </section>
        </>
    )
}   
export default Main;